*{
	padding: 0;
	margin: 0;
	text-decoration: none;
	list-style: none;
	box-sizing: border-box;
}

.data{
	/*background: url("images/shivji.jpg");*/
	height: 100vh;
	margin-left: 250px;
	background-size: cover;
	background-repeat: no-repeat;
	position: fixed;
	margin-top: 80px;
}
.data img{
	width: 100vw;
	height: 100%;
}

nav{
	background: #1D2951;
	line-height: 80px;
	position: fixed;
	width: 100%;
}

label{
	line-height: 80px;
	font-size: 30px;
	color: white;
	font-weight: 200;
	margin-left: 50px;
	cursor: pointer;
}

nav ul{
	  float: right;
	  margin-right: 20px;
}

nav ul li{
	line-height: 80px;
	margin: 0 10px;
}

nav ul li a{
	color: white;
	font-size: 20px;
	padding: 6px 15px;
	font-weight: 600;
	background-color: rgb(55,34,246);
	border-radius: 3px;
}

nav ul li a:hover{
	background-color: red;
	color: white;
	transition: 0.5s;
}

.footer{
		background-color: #1D2951;
		height: 100px;
		position: fixed;
		width: 100vw;
		margin-top: 650px;
	}
.footer p{
	text-align: center;
	font-size: 20px;
	padding: 10px;
}

.side-menu{
	    position: fixed;
	    background: #1D2951;
	    width: 250px;
	    height: 100vh;
	    margin-top: 50px;
}

.side-menu center img{
	height: 100px;
	width: 120px;
	border-radius: 50%;
	margin-top: 20px;
	border:3px solid white;
}

.side-menu center h2{
	color: white;
}

.side-menu a{
	display: block;
	line-height: 45px;
	transition: 0.5s;
}

.side-menu a:hover{
	background: rgb(20,54,165);
	padding-left: 20px;
}

.side-menu span{
	font-size: 20px;
	margin-left: 10px;
	color: white;
}

.side-menu i{
	font-size: 20px;
	margin-left: 20px;
	color: white;
}

nav .menu-bar{
	float: right;
	/*margin-top: 20px;*/
	display: none; 
	margin-right: 20px; 
}

#menu{
	display: none;
}

.side-menu .logout{
	display: none;
}


@media(max-width: 850px){
	.footer{
		display: none;
	}
	.side-menu a span{
		display: none;
	}

	.side-menu center{
		display: none;
	}

	.side-menu{
		width: 100px;
	}

	.side-menu a i{
		display: block;
		line-height: 80px;
		text-align: center;
		margin-left: 0;
		font-size: 35px;
	}

	.data{
		margin-left: 100px;
	}

	@media(max-width: 650px){
		.footer{
			margin-top: 570px;
		}
		.data{
			display: none;
		}

		nav ul{
			display: none;
		}

		.side-menu{
			width: 100%;
			left: 100%;
			text-align: center;
			background-color: #84c516d; 
			transition: all 0.5s;
		}

		.side-menu a i{
			display: none;
		}

		.side-menu a span{
			display: block;
		}

		.side-menu .logout{
			display: block;
		}

		nav .menu-bar{
			display: block;
		}

		#menu:checked ~ .side-menu{
			left: 0;

		}

	}

}